#session-length{
	width : 60px;
}

.length-container{
	display: -ms-flexbox;
	display: flex;	
	width:500px;
	-ms-flex-pack: justify;
	    justify-content: space-between;
	margin-top: 20px;
	font-size: 30px;
	font-weight: 600;
}


@media (max-width:501px){
	.length-container{
		width: 100%;
		-ms-flex-direction: column;
		    flex-direction: column;
		-ms-flex-align: center;
		    align-items: center;
	}
}


